/* 
--------------------------------------------------------------------------------
Bespoke reset
--------------------------------------------------------------------------------
*/
html {
    /* border-box box model allows us to add padding and border to our elements without increasing their size */
    box-sizing: border-box;
}
*, *::before, *::after {
    box-sizing: inherit;
}
a {
    text-decoration:none;
    color:inherit;
    cursor:pointer;
}
button {
    background-color:transparent;
    color:inherit;
    border-width:0;
    padding:0;
    cursor:pointer;
}
figure {
    margin:0;
}
input::-moz-focus-inner {
    border:0;
    padding:0;
    margin:0;
}
ul, ol, dd{margin:0; padding:0; list-style:none;}
h1, h2, h3, h4, h5, h6{
    margin:0;
    font-size:inherit;
    font-weight:inherit;
}
p {
    margin:0;
}
cite {
    font-style:normal;
}
fieldset {
    border-width:0; padding:0; margin:0;
}
